*{
    list-style-type: none;
    color:rgb(255, 255, 255);
}
.primaryData{
    font-weight: bold;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    width:60%;
    height: auto;
    display:flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
#temp{
    font-size: 60px;
}
.city{
    font-size:130%;
}
.section{
    border-radius: 8px;
    background-color: rgba(125, 109, 109, 0.6);
    margin-top: 160px;
    font-weight: bold;
    width:75%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: nowrap;
    filter: blur();
    height:120px;
}
.section:hover{
    background-color: rgba(96, 81, 81, 0.6);
}
.h2{
    font-size: 42px;
}
.center{
    text-align:center;
}
.section > *{
    margin-top: auto;
    margin-bottom: auto;
}
@media (max-width:500px)
{
    .primaryData{
        width:100%;
    }
    .section{
        width:100%;
    }
}
@media (max-width:650px) and (min-width:501px)
{
    .primaryData{
        width:90%;
    }
    .section{
        width:90%;
    }
}
@media (max-width:400px)
{
    .primaryData{
        justify-content: space-between;
    }
    .section{
        justify-content: space-between;
    }
    .h2{
        font-size: 130%;
    }
    .section{
        height:80px;
    }
    #temp{
        font-size: 40px;
    }
    .form{
        justify-content: flex-start;
    }
}
.city{
    color:maroon;
}
/*        FORM STYLES          */
.input{
    background-color: rgba(125, 109, 109, 0.6);
    border:none;
    outline: none;
    width:260px;
    height:40px;
    text-align:center;
    border-radius: 5px;
    color: ghostwhite;
}
.input:hover{
    background-color: rgb(40, 38, 38);
    border:none;
    outline: none;
}
.input:active{
    background-color: rgb(37, 35, 35);
}
   .form{
    display:flex;
    justify-content: flex-end;
   }
   .left{
    text-align:left;
   }